import React from 'react'


const styles = {
    imageContainer: {
        marginBottom: '10px',
        display: 'flex',
        background: '#f8e9dc',
        padding: '10px',
        borderRadius: '10px',
        border:"3px solid white",
        boxShadow: " 0 0 20px #777",

    },
    imageLeft: {
        float: 'left',
        position: 'absolute',
        left: '30%',
    },
    imageCenter: {
        display: 'inline-block',
        margin: '0 auto',
    },
    imageRight: {
        float: 'right',
        position: 'absolute',
        right: '30%'
    },
    image: {
        maxWidth:'100%',
        objectFit: 'scaleDown',
        borderRadius: '50%',
        width: '30px',
        height: '30px',
    }
}

function Footer() {
    const openInNewTab = (url) => {
        const newWindow = window.open(url, '_blank', 'noopener,noreferrer')
        if (newWindow) newWindow.opener = null
    }
    return (
        <div style={styles.imageContainer}>
            <div style={styles.imageLeft}>
                <img style={styles.image}
                class='popping'
                src="data:image/png;base64,"
                onClick={()=> openInNewTab('https://github.com/IvannikovG')}
                ></img>
            </div>
            <div style={styles.imageCenter}>
                <img style={styles.image}
                 class='popping'
                 src="https://e7.pngegg.com/pngimages/683/995/png-clipart-vkontakte-logo-facebook-messenger-computer-icons-facebook-blue-logo.png"
                 onClick={()=> openInNewTab('https://vk.com/hs_tory')}></img>
            </div>
            <div style={styles.imageRight}>
                <img style={styles.image}
                 class='popping'
                 src="https://pureoxygenlabs.com/wp-content/uploads/2017/10/telegram-logo-250x250.png"
                 onClick={()=> openInNewTab('https://t.me/Georgii_i')}
                 ></img>
            </div>
        </div>
    )
}


export default Footer